import React from 'react'
import Confetti from 'react-confetti'

import { Dialog, DialogTitle, DialogContent, DialogActions, Link, CircularProgress, Button } from '@material-ui/core'
import { SaltSeason } from '../models/salt_season'

export type State = 'MINING' | 'FAILED' | 'SUCCESS'

type Props = {
  // saltSeason: SaltSeason
  // tokenId: number
  state: State
}

function Success(props: Props) {
  const [open, setOpen] = React.useState(true)

  switch(props.state) {
    case 'MINING':
      return (
        <Dialog open={true}>
          <DialogTitle>Please wait</DialogTitle>
          <DialogContent style={{ padding: 30 }}>
            <CircularProgress/>
          </DialogContent>
        </Dialog>)

    case 'FAILED':
      return (
        <Dialog open={open} onClose={() => setOpen(false)}>
          <DialogTitle>Oops</DialogTitle>
          <DialogContent style={{ padding: 30 }}>
            <p></p>
          </DialogContent>
          <DialogActions style={{ padding: 30 }}>
            <Button onClick={() => setOpen(false)}>Close</Button>
          </DialogActions>
        </Dialog>)

    case 'SUCCESS':
      return (
        <>
          <Dialog open={open} onClose={() => setOpen(false)} maxWidth="lg">
            <DialogTitle>Hooray!</DialogTitle>
            <DialogContent style={{ padding: 30 }}>
              <Confetti/>
              <p>You are the owner of unique SaltSeason</p><br/>
              <p>Now you can <a href="https://redeem.saltandsatoshi.com/">redeem</a> your purchase</p>
            </DialogContent>
            <DialogActions style={{ padding: 30 }}>
              <Link href="https://redeem.saltandsatoshi.com/">Redeem</Link>
            </DialogActions>
          </Dialog>
        </>)
  }
}

export default Success