import React from 'react'

import { useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'

import * as WEB3HOOKS from '../services/web3_hooks'
import Loading from '../components/loading'
import Connect from '../components/connect'

import * as SaltSeason from '../models/salt_season'
import SaltSeasonComponent from './_salt_season'

import { Box } from '@material-ui/core'

const DEFAULT_CHAIN_ID = 42

function Reducer() {
  const context = useWeb3React<Web3Provider>()
  const { connector, chainId } = context

  // handle logic to recognize the connector currently being activated
  const [activatingConnector, setActivatingConnector] = React.useState<any>()
  React.useEffect(() => {
    if (activatingConnector && activatingConnector === connector) {
      setActivatingConnector(undefined)
    }
  }, [activatingConnector, connector])

  // handle logic to eagerly connect to the injected ethereum provider, if it exists and has granted access already
  const triedEager = WEB3HOOKS.useEagerConnect()

  // handle logic to connect in reaction to certain events on the injected ethereum provider, if it exists
  WEB3HOOKS.useInactiveListener(!triedEager || !!activatingConnector)

  const [saltSeasons, setSaltSeasons] = React.useState<SaltSeason.SaltSeason[] | undefined>(undefined)
  React.useEffect(() => { // Load auctions from Firebase
    if (!!saltSeasons) { return } // guard: already done
    // if (!chainId) { return }

    (async () => {
      const items = await SaltSeason.indexByNetworkId(chainId || DEFAULT_CHAIN_ID)
      setSaltSeasons(items)
    })()
  }, [saltSeasons, chainId])

  if (!saltSeasons) { return <Loading/> }

  // Dirty
  (document.querySelector("#main > .inner") as any).style.paddingLeft = 0;
  (document.querySelector("#main > .inner") as any).style.paddingRight = 0;

  return (
    <div>
      <section id="redemption-section">
        <div id="container19" className="container default full screen" style={{minHeight: 'auto'}}>
          <div className="wrapper">
            <div className="inner" style={{padding: '3em 0em 1em 0em'}}>
              <h2 id="text33" style={{ fontWeight: 'normal', textTransform: 'none', color: '#fff' }} className="with-cool-font">Salt Seasons</h2>
              
              <div style={{ fontWeight: 'normal', color: '#878E99' }} className="style2 with-cool-font">
                <p>Welcome to Salt Seasons - join us on the quest to close the divide between physical and digital collections.</p>
                <br/>
                <p>The first Salt Season, "Can't Be Stopped" consists of two editions of 20 unique collectible hoodies, each linked 1:1 to an NFT which is verifiable via an NFC tag.</p>
              </div>
              
              <Connect/>
            </div>
          </div>
        </div>
      </section>

      <Box display="flex" flexDirection="column" style={{marginTop: '3em', color: 'lightgray'}}>
        {
          saltSeasons.map(saltSeason => (
            <SaltSeasonComponent saltSeason={saltSeason}/>
          ))
        }
      </Box>
    </div>)
}

export default Reducer