import React from 'react'

import { useWeb3React } from '@web3-react/core'
import { TransactionReceipt, TransactionResponse, Web3Provider } from '@ethersproject/providers'

import {  Box, CircularProgress, Chip, Icon } from '@material-ui/core'
import ImageGallery, { ReactImageGalleryItem } from 'react-image-gallery'

import config from '../config'

import { Contract, Event } from '@ethersproject/contracts'
import ETHERIUM from '../services/etherium'
import SELLERV1 from '../services/etherium/contracts/sellerv1'
import Device from '../services/device'
import ETH from '../components/eth'
import * as SaltSeason from '../models/salt_season'
import { BigNumber } from '@ethersproject/bignumber'
import Success, { State as SuccessState } from './_success'

type Props = {
  saltSeason: SaltSeason.SaltSeason
}

function SaltSeasonComponent(props: Props) {
  const { saltSeason } = props
  const provider = ETHERIUM.getJsonRpcProvider(config.INFURA_JSON_RPC_API[saltSeason.networkId], saltSeason.networkId);

  const context = useWeb3React<Web3Provider>()
  const { account, library } = context

  const [contract, setContract] = React.useState<Contract | undefined>(undefined)
  React.useEffect(() => {
    if (contract !== undefined) { return }
    const _contract = SELLERV1.getContract(saltSeason.sellerContractAddress, provider)
    setContract(_contract)
  }, [contract])

  const [contractToWrite, setContractToWrite] = React.useState<Contract | undefined>(undefined)
  React.useEffect(() => {
    if (contractToWrite !== undefined) { return }
    if (!account || !library) { return }

    const signer = library.getSigner(account)
    const _contract = SELLERV1.getContract(saltSeason.sellerContractAddress, signer)
    setContractToWrite(_contract)
  }, [contractToWrite, account])

  const [currentPrice, setCurrentPrice] = React.useState<BigNumber | undefined>(undefined)
  React.useEffect(() => {
    if (!!currentPrice) { return } // guard: already done
    if (!contract) { return } // guard

    (async () => {
      const currentPrice = await contract.currentPrice()
      setCurrentPrice(currentPrice)
    })()
  }, [currentPrice, contract])

  const [priceStep, setPriceStep] = React.useState<BigNumber | undefined>(undefined)
  React.useEffect(() => {
    if (!!priceStep) { return } // guard: already done
    if (!contract) { return } // guard

    (async () => {
      const priceStep = await contract.priceStep()
      setPriceStep(priceStep)
    })()
  }, [priceStep, contract])

  const [tokenPurchaseEvents, setTokenPurchaseEvents] = React.useState<Event[] | undefined>(undefined)
  React.useEffect(() => {
    if (!contract) { return }
    if (tokenPurchaseEvents !== undefined) { return }
    (contract as any).queryFilter(contract.filters.TokenPurchase).then((events: Event[]) => {
      // console.log(events)
      setTokenPurchaseEvents(events.filter(e => (e as any).event === "TokenPurchase"))
    })
  }, [contract, tokenPurchaseEvents])

  const [notice, setNotice] = React.useState<string | undefined>(undefined)
  const [successState, setSuccessState] = React.useState<SuccessState | undefined>(undefined)

  const tokensPurchased = (tokenPurchaseEvents || []).length
  const seasonIsOver = saltSeason.tokenIds.length <= tokensPurchased

  const isReady = (tokenPurchaseEvents !== undefined) && !!currentPrice
  const currentTokenId = !seasonIsOver ? saltSeason.tokenIds[tokensPurchased] : undefined

  const [showMetamaskHint, setShowMetamaskHint] = React.useState<boolean>(false)

  const purchase = async () => {
    if (!account) { throw("Connect to blockchain (no account)") }
    if (!currentPrice) { throw("Connect to blockchain (no contract data)") }
    if (!contractToWrite) { throw("Connect to blockchain (no contract)") }
    if (!currentTokenId) { throw("Season is over") }

    // for test
    // const transactionResponse: TransactionResponse = await (contractToWrite as any).sendTo('0xC5Ddcb2812dd0D6a61a94310E4AA57c5122438Ca', 1)

    const transactionResponse: TransactionResponse = await (contractToWrite as any).purchaseToken(saltSeason.erc721ContractAddress, currentTokenId, {
      value: currentPrice
    })

    setSuccessState('MINING')

    const transactionReceipt: TransactionReceipt = await transactionResponse.wait(1)
    if (transactionReceipt.status === 0) { throw 'Transaction was reverted' }

    // saltSeason.purchasedItems += 1
    // await SaltSeason.update(saltSeason)
  }

  // const carouselRef = React.createRef<HTMLDivElement>()
  // React.useEffect(() => {
  //   if(!!carouselRef.current) {
  //     carouselRef.current.querySelectorAll(`[role=presentation]`)[0].setAttribute("style", "position: absolute; width: 100%; height: 100%;");
  //   }
  // }, [])

  const isMobile = Device.isMobile();

  return (
    <>
      <Box display="flex" flexDirection={isMobile ? 'column' : 'row'} justifyContent="center" style={{marginBottom: '5em', width: '100%'}}>
        <div style={isMobile ? { padding: '1em' } : { flexGrow: 1, width: '50%' }}>
          <ImageGallery
            showThumbnails={false}
            showPlayButton={false}
            items={saltSeason.posterUrls.map(posterUrl => ({ original: posterUrl }))}
            renderItem={(item: ReactImageGalleryItem) => {
              return <div
                style={{
                  backgroundImage: `url(${item.original})`,
                  backgroundPosition: 'center',
                  backgroundSize: 'contain',
                  backgroundRepeat: 'no-repeat',
                  height: '500px',
                  width: '100%'
                }}
              />
            }}
          />
        </div>

        <div style={isMobile ? { padding: '1em' } : { flexGrow: 1, width: '50%', padding: '0 3em' }} className="with-cool-font">
          <p style={{ fontSize: '2em', color: '#fff' }}>Season {saltSeason.number}</p><br/>
          <h5>{saltSeason.title}</h5><br/>
          
          <div 
            style={{ fontWeight: 'normal', color: '#878E99', textAlign: 'justify', fontSize: 'smaller' }}
            className="style2 with-cool-font"
            dangerouslySetInnerHTML={{ __html: saltSeason.descriptionHtml }}
          />
          
          <br/>
          <p>Edition of {saltSeason.tokenIds.length}</p><br/>
          <p>Price {!currentPrice ? <CircularProgress size={15}/> : <ETH.Number number={currentPrice}/>}</p><br/>
          {(!!priceStep && !!currentPrice && !!tokenPurchaseEvents) && (
            <>
              <p>Price increment<sup>*</sup> <ETH.Number number={priceStep}/></p><br/>
              <p style={{fontSize: 'small', color: '#878E99'}}><sup>*</sup>&nbsp;The price begins at <ETH.Number number={currentPrice.sub(priceStep.mul(tokenPurchaseEvents.length))}/> and will increase by <ETH.Number number={priceStep}/> after each purchase to an end price of <ETH.Number number={currentPrice.add(priceStep.mul(saltSeason.tokenIds.length - tokenPurchaseEvents.length))}/></p><br/>
            </>) }
          <Box display="flex" flexDirection="row" justifyContent="between">
            { !isReady && <CircularProgress size="20"/> }
            { isReady && (
              <>
                { seasonIsOver && (
                  <ul id="buttons02" className="style1 buttons">
                    <li>
                      <a href="#" className="button n01">
                        <span className="label">Season is over</span>
                        <Icon style={{marginRight: '0.5em'}}>done</Icon>
                      </a>
                    </li>
                  </ul>)
                } 
                { !seasonIsOver && (
                  <ul id="buttons02" className="style1 buttons">
                    <li>
                      <a href="#" className="button n01"
                        onClick={async (event) => {
                          event.stopPropagation()
                          setNotice(undefined)
                          try {
                            setShowMetamaskHint(true)
                            await purchase()
                            setSuccessState('MINING')
                          } catch(error) {
                            setSuccessState(undefined)
                            console.error("[ERROR]", error)
                            setNotice(`${error.message || error}`)
                            return false
                          } finally {
                            setShowMetamaskHint(false)
                          }
                          setSuccessState('SUCCESS')
                          return false
                        }}
                      ><span className="label">Purchase</span><Icon style={{marginRight: '0.5em'}}>shopping_cart</Icon></a>
                    </li>
                    <li>
                      <a href="https://redeem.saltandsatoshi.com/" target="_blank"
                        className="button n01"
                      ><span className="label">Redeem</span><Icon style={{marginRight: '0.5em'}}>local_shipping</Icon></a>
                    </li>
                  </ul>
                ) }
              </>
            ) }
          </Box>
          { notice && <Chip label={notice} color="secondary"/> }
        </div>

        {/* <div style={{position: 'relative', height: 600, flexGrow: '65%'}} ref={carouselRef}>
          <AutoRotatingCarousel
            open={true}
            autoplay={true}
            containerStyle={{position: 'absolute', top: 0, width: '100%'}}
            ModalProps={{disablePortal: true, hideBackdrop: true, disableScrollLock: true}}
            ButtonProps={{variant: 'outlined'}}
            interval={5000}
          >
            <Slide
              media={
                <div
                  style={{
                    backgroundImage: `url(${saltSeason.posterUrl})`,
                    backgroundPosition: 'center',
                    backgroundSize: 'contain',
                    backgroundRepeat: 'no-repeat',
                    height: '100%',
                    width: '100%'
                  }}
                />
              }
              mediaBackgroundStyle={{ backgroundColor: 'black', height: 400 }}
              style={{ backgroundColor: 'black' }}
              title={`SEASON ${saltSeason.number}`}
              mobile
              landscape
              // subtitle='Just using this will blow your mind.'
            />
            <Slide
              media={
                <div
                  style={{
                    backgroundImage: `url(${"https://firebasestorage.googleapis.com/v0/b/saltandsatoshi.appspot.com/o/SaltSeasons%2FSalt%20Season%201%20Ethereum%20GIF%20600.gif?alt=media&token=2ed90c2b-2864-40bc-9710-b564348c33a0"})`,
                    backgroundPosition: 'center',
                    backgroundSize: 'contain',
                    backgroundRepeat: 'no-repeat',
                    height: '100%',
                    width: '100%'
                  }}
                />
              }
              mediaBackgroundStyle={{ backgroundColor: 'black', height: 400 }}
              style={{ backgroundColor: 'black' }}
              title={`SEASON ${saltSeason.number}`}
              // subtitle='Just using this will blow your mind.'
            />
          </AutoRotatingCarousel> 
        </div> */}
      </Box>
      { successState && <Success state={successState}/> }
      { showMetamaskHint && <div className="metmask-hint"><span>Please confirm a pending Ethereum transaction.</span></div> }
    </>)
}

export default SaltSeasonComponent